.create-poll-container {
  display: flex;
  width: 100%;
  justify-content: space-between;
}

.create-poll-form-col {
  width: 70%;
  background: #ffffff;
  box-shadow: 0px 4px 12px rgba(0, 0, 0, 0.1);
  border-radius: 6px;
  height: 100vh;
  display: flex;
  justify-content: space-between;
  padding: 20px 30px 10px 30px;
}

.pull-action {
  cursor: pointer;
}
.poll-filter-container {
  display: inline-block;
  margin: 5px;
}
.danger {
  color: red;
}
.cursor {
  cursor: pointer;
}
.create-poll-form-col label {
  font-weight: 600;
  font-size: 17px;
  line-height: 25px;
  color: #002233;
  margin-bottom: 15px;
}

.create-poll-preview {
  width: 29%;
  background: #ffffff;
  border: 0.5px solid #c4c4c4;
  box-sizing: border-box;
  border-radius: 6px;
}

.createPollForm-col-one-child {
  width: 90%;
}
.createPollForm-col-one-child form {
  width: 100%;
}
.createPollForm-col-one-child form .poll-input-wrap {
  width: 90%;
  display: flex;
  flex-direction: column;
}
.createPollForm-col-one-child form .poll-input-wrap textarea {
  background: rgba(229, 229, 229, 0.24);
  border: 1px dashed #c4c4c4;
  box-sizing: border-box;
  border-radius: 4px;
  width: 100%;
  padding: 10px;
  height: 124px;
  outline: none;
}

.createPollForm-col-two-child {
  width: 28%;
}
.createPollForm-col-two-child form {
  display: flex;
  width: 100%;
  flex-direction: column;
}
.selet-poll-type select {
  width: 100%;
  height: 40px;
  background: #e7f5ff;
  border-radius: 5px;
  border: none;
  color: #002233;
  margin-bottom: 20px;
}

.poll-options {
  width: 100%;
  margin-top: 15px;
}

.poll-input-add {
  display: flex;
}

.poll-input-add input {
  width: 80%;
  background: #f2f2f2;
  border: 1px solid rgba(196, 196, 196, 0.5);
  box-sizing: border-box;
  border-radius: 6px;
  height: 50px;
  padding-left: 15px;
  outline: none;
}

.poll-input-add img {
  width: 30px;
  height: 30px;
  margin-left: 11px;
  margin-top: 11px;
  cursor: pointer;
}

.poll-options-drow {
  position: relative;
  margin: o;
}

.poll-options-drow {
  position: relative;
  width: 80%;
}

.poll-options-drow input {
  position: relative;
  width: 100%;
  background: #f2f2f2;
  border: none;
  box-sizing: border-box;
  height: 50px;
  padding-left: 15px;
  outline: none;
}

.poll-options-drow img {
  width: 20px;
  height: 20px;
  cursor: pointer;
  position: absolute;
  top: 16px;
  right: 0;
  right: 15px;
}

.poll-tags {
  margin-top: 15px;
}

.save-poll-btn {
  display: flex;
  width: 147px;
  margin-top: 15px;
  height: 50px;
  margin-top: 20px;
}

.save-poll-btn button {
  width: 100%;
  background: #003766;
  border-radius: 4px;
  border: none;
  color: white;
}
.poll-save {
  display: flex;
  flex-direction: column;
}

/* The switch - the box around the slider */
.switch {
  position: relative;
  display: inline-block;
  width: 60px;
  height: 34px;
}

/* Hide default HTML checkbox */
.switch input {
  opacity: 0;
  width: 0;
  height: 0;
}

/* The slider */
.slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #ccc;
  -webkit-transition: 0.4s;
  transition: 0.4s;
}

.slider:before {
  position: absolute;
  content: "";
  height: 26px;
  width: 26px;
  left: 4px;
  bottom: 4px;
  background-color: white;
  -webkit-transition: 0.4s;
  transition: 0.4s;
}

input:checked + .slider {
  background-color: #2196f3;
}

input:focus + .slider {
  box-shadow: 0 0 1px #2196f3;
}

input:checked + .slider:before {
  -webkit-transform: translateX(26px);
  -ms-transform: translateX(26px);
  transform: translateX(26px);
}

/* Rounded sliders */
.slider.round {
  border-radius: 34px;
}

.slider.round:before {
  border-radius: 50%;
}
